import { computed, defineComponent, nextTick, onBeforeUnmount, onMounted, ref, type PropType, Teleport, Transition } from 'vue'

export type TooltipPosition = 'top' | 'bottom'

export const Tooltip = defineComponent({
  name: 'Tooltip',
  props: {
    content: {
      type: String,
      default: ''
    },
    position: {
      type: String as PropType<TooltipPosition>,
      default: 'top'
    },
    offset: {
      type: Number,
      default: 10
    }
  },
  setup(props, { slots }) {
    const tooltipContainer = ref<HTMLElement | null>(null)
    const tooltip = ref<HTMLElement | null>(null)
    const isVisible = ref(false)
    const positionStyle = ref<{ top?: string; left?: string; position?: string; zIndex?: string }>({})
    const currentPosition = ref<TooltipPosition>(props.position)

    // ä½¿ç”¨å›ºå®šç±»åè€ŒéåŠ¨æ€ç±»åï¼Œç¡®ä¿ Tailwind å¯ä»¥è¯†åˆ«
    const tooltipClasses = computed(() => [
      'absolute px-5 py-3 text-center text-gray-600 bg-white rounded-lg shadow-lg dark:text-white dark:bg-gray-800 dark:shadow-none shadow-gray-200',
      'transition-all duration-200 ease-out z-50',
      'min-w-[12rem] max-w-xs',
      `tooltip-${currentPosition.value}`
    ])

    // ç®­å¤´æ ·å¼å•ç‹¬å¤„ç†ï¼Œé¿å…åŠ¨æ€ç±»é—®é¢˜
    const arrowClasses = computed(() => [
      `tooltip-arrow-${currentPosition.value}`
    ])

    // ä½¿ç”¨è®¡ç®—å±æ€§å¤„ç†å®šä½æ ·å¼
    const tooltipStyles = computed(() => ({
      position: 'fixed' as const,
      top: positionStyle.value.top,
      left: positionStyle.value.left,
      zIndex: 9999,
    }))

    const calculatePosition = () => {
      if (!tooltipContainer.value || !tooltip.value) return

      // é‡ç½®å½“å‰ä½ç½®ä¸ºåˆå§‹è®¾ç½®çš„ä½ç½®
      currentPosition.value = props.position

      // è·å–è§¦å‘å…ƒç´ çš„ä½ç½®ä¿¡æ¯
      const triggerRect = tooltipContainer.value.getBoundingClientRect()
      const tooltipRect = tooltip.value.getBoundingClientRect()

      // è§†å£å°ºå¯¸
      const viewportWidth = document.documentElement.clientWidth
      const viewportHeight = document.documentElement.clientHeight

      // å®‰å…¨è¾¹è·ï¼Œé¿å…è´´è¾¹
      const safeMargin = 8

      let top = 0
      let left = 0

      // åŸºç¡€ä½ç½®è®¡ç®— - æ°´å¹³å±…ä¸­å¯¹é½
      left = triggerRect.left + (triggerRect.width - tooltipRect.width) / 2

      // æ ¹æ®æŒ‡å®šä½ç½®ç¡®å®šå‚ç›´æ–¹å‘çš„ä½ç½®
      if (currentPosition.value === 'top') {
        // è§¦å‘å…ƒç´ ä¸Šæ–¹
        top = triggerRect.top - tooltipRect.height - props.offset
      } else {
        // è§¦å‘å…ƒç´ ä¸‹æ–¹
        top = triggerRect.bottom + props.offset
      }

      // æ™ºèƒ½æ–¹å‘åˆ‡æ¢ - å¦‚æœæŒ‡å®šä½ç½®æ”¾ä¸ä¸‹ï¼Œåˆ™åˆ‡æ¢åˆ°åæ–¹å‘
      if (top < safeMargin && currentPosition.value === 'top') {
        // ä¸Šæ–¹ç©ºé—´ä¸è¶³ï¼Œåˆ‡æ¢åˆ°ä¸‹æ–¹
        currentPosition.value = 'bottom'
        top = triggerRect.bottom + props.offset
      } else if (top + tooltipRect.height > viewportHeight - safeMargin && currentPosition.value === 'bottom') {
        // ä¸‹æ–¹ç©ºé—´ä¸è¶³ï¼Œåˆ‡æ¢åˆ°ä¸Šæ–¹
        currentPosition.value = 'top'
        top = triggerRect.top - tooltipRect.height - props.offset
      }

      // æ¨ªå‘è¾¹ç•Œè°ƒæ•´ - ç¡®ä¿ä¸è¶…å‡ºè§†å£
      if (left < safeMargin) {
        // å·¦è¾¹ç•Œè°ƒæ•´ï¼Œç¡®ä¿è‡³å°‘ç•™æœ‰å®‰å…¨è¾¹è·
        left = safeMargin
      } else if (left + tooltipRect.width > viewportWidth - safeMargin) {
        // å³è¾¹ç•Œè°ƒæ•´ï¼Œç¡®ä¿ä¸è¶…å‡ºå³è¾¹ç•Œ
        left = viewportWidth - tooltipRect.width - safeMargin
      }

      // æç«¯æƒ…å†µå¤„ç† - å¦‚æœä¸Šä¸‹éƒ½æ”¾ä¸ä¸‹
      if (top < safeMargin && top + tooltipRect.height > viewportHeight - safeMargin) {
        // å¦‚æœæ˜¾ç¤ºåŒºåŸŸå¤ªå°ï¼Œä¼˜å…ˆç¡®ä¿å†…å®¹æœ€å¤§ç¨‹åº¦å¯è§
        if (currentPosition.value === 'top') {
          top = safeMargin
        } else {
          top = viewportHeight - tooltipRect.height - safeMargin
        }
      } else {
        // æ­£å¸¸è¾¹ç•Œè°ƒæ•´
        if (top < safeMargin) {
          top = safeMargin
        }
        if (top + tooltipRect.height > viewportHeight - safeMargin) {
          top = viewportHeight - tooltipRect.height - safeMargin
        }
      }

      // è®¾ç½®æœ€ç»ˆä½ç½®ï¼Œä½¿ç”¨ fixed å®šä½ä»¥å¤„ç†æ»šåŠ¨
      positionStyle.value = {
        position: 'fixed',
        top: `${top}px`,
        left: `${left}px`,
        zIndex: '9999',
      }
    }

    // æ˜¾ç¤ºæç¤º
    const showTooltip = () => {
      isVisible.value = true
      nextTick(() => {
        calculatePosition()
        // é¦–æ¬¡è®¡ç®—åï¼Œå†æ‰§è¡Œä¸€æ¬¡è®¡ç®—ä»¥ç¡®ä¿ä½ç½®å‡†ç¡®
        nextTick(() => {
          calculatePosition()
        })
      })
    }

    // éšè—æç¤º
    const hideTooltip = () => {
      isVisible.value = false
    }

    // ç›‘å¬å„ç§å¯èƒ½å¯¼è‡´ä½ç½®å˜åŒ–çš„äº‹ä»¶
    onMounted(() => {
      window.addEventListener('resize', calculatePosition)
      window.addEventListener('scroll', calculatePosition, { passive: true })
    })

    onBeforeUnmount(() => {
      window.removeEventListener('resize', calculatePosition)
      window.removeEventListener('scroll', calculatePosition)
    })

    return () => (
      <div class="relative inline-block" ref={tooltipContainer}>
        <div
          onMouseenter={showTooltip}
          onMouseleave={hideTooltip}
          class="w-full"
        >
          {slots.trigger ? (
            slots.trigger()
          ) : (
            <button
              type="button"
              class="text-gray-600 transition-colors duration-200 focus:outline-none dark:text-gray-300 dark:hover:text-blue-400 hover:text-blue-500"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="2"
                stroke="currentColor"
                class="w-6 h-6"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M9.879 7.519c1.171-1.025 3.071-1.025 4.242 0 1.172 1.025 1.172 2.687 0 3.712-.203.179-.43.326-.67.442-.745.361-1.45.999-1.45 1.827v.75M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9 5.25h.008v.008H12v-.008z"
                />
              </svg>
            </button>
          )}
        </div>

        <Teleport to="body">
          <Transition
            name="tooltip-fade"
            enterActiveClass="transition ease-out duration-200"
            enterFromClass="opacity-0 scale-95"
            enterToClass="opacity-100 scale-100"
            leaveActiveClass="transition ease-in duration-150"
            leaveFromClass="opacity-100 scale-100"
            leaveToClass="opacity-0 scale-95"
          >
            {isVisible.value && (
              <div
                ref={tooltip}
                class={tooltipClasses.value}
                style={tooltipStyles.value}
              >
                {slots.content ? (
                  slots.content()
                ) : (
                  <p class="text-sm">{props.content}</p>
                )}
                {/* æ·»åŠ ç®­å¤´å…ƒç´  */}
                <span class={arrowClasses.value}></span>
              </div>
            )}
          </Transition>
        </Teleport>

        <style scoped>{`
          /* ç®­å¤´æ ·å¼ - ä½¿ç”¨å›ºå®šç±»å */
          .tooltip-top::after,
          .tooltip-arrow-top {
            content: "";
            position: absolute;
            bottom: -8px;
            left: 50%;
            margin-left: -8px;
            border-width: 8px 8px 0;
            border-style: solid;
            border-color: #ffffff transparent transparent transparent;
          }
          
          .tooltip-bottom::after,
          .tooltip-arrow-bottom {
            content: "";
            position: absolute;
            top: -8px;
            left: 50%;
            margin-left: -8px;
            border-width: 0 8px 8px;
            border-style: solid;
            border-color: transparent transparent #ffffff transparent;
          }
          
          /* æš—é»‘æ¨¡å¼ç®­å¤´é¢œè‰² - ä½¿ç”¨å…¨å±€é€‰æ‹©å™¨é€‚é… Tailwind dark æ¨¡å¼ */
          :global(.dark) .tooltip-top::after,
          :global(.dark) .tooltip-arrow-top {
            border-color: #1f2937 transparent transparent transparent;
          }
          
          :global(.dark) .tooltip-bottom::after,
          :global(.dark) .tooltip-arrow-bottom {
            border-color: transparent transparent #1f2937 transparent;
          }
        `}</style>
      </div>
    )
  }
})

export default Tooltip
